@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap);
/* define all global fonts, typefaces and colors here */

:root {

    /* Typefaces */

    --bigHeadingFontFamily : 'Lexend Deca', sans-serif;
    --mediumHeadingFontFamily : 'Montserrat', sans-serif;
    --smallHeadingFontFamily: 'Poppins', sans-serif;

    --darkText : #2a2b2b;
    --tanText : #434343;
    --darkGrey: #878787;
    --grey: #ababab;
    --lightGrey: #d3d3d3;
    --ultraLightGrey: #eeeeee;

    --verySmallText: 13.5px;
    --smallText: 15px;
    --heading6 : 17px;
    --heading5: 19px;
    --heading4: 25px;
    --heading3: 30px;
    --heading2: 40px;
    --heading1: 50px;

    --bold: 700;
    --medium: 600;
    --regular: 500;
    --light: 400;

    /* Colors */
    --googleBlue : #4285F4;
    --googleRed : #DB4437;
    --googleGreen : #0F9D58;
    --googleYellow : #F4B400;
}

.main-container {
    margin: 0;
    padding: 0;
}

html{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

a {
    text-decoration: none;
}
.header-main-container {
    width: 100%;
    min-height: 60px;
    background-color: #ffffff;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
    padding: 0 10%;
    justify-content: space-between;
    position: fixed;
    z-index: 100;
    transition: 0.3s ease-in-out;
}

.header-options ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.header-options ul li  {
    list-style-type: none;
    margin: auto 20px;
    padding: 5px 10px;
    background-color: transparent;
    border-radius: 50px;
    font-family: var(--mediumHeadingFontFamily);
    font-size: var(--heading6);
    font-weight: var(--regular);
    transition: 0.2s ease-in-out;
    cursor: pointer;
    text-decoration: none;
    color: var(--darkText)
}
.header-options ul li:hover {
    background-color: var(--ultraLightGrey);
    transition: 0.2s ease-in-out;
}
.link {
    color: var(--darkText)
}

.header-logo {
    width: auto;
}

.header-logo img {
    width: 350px;
    visibility: visible;
    transition: visibility 0s, opacity 2.5s ease-in-out;
    margin-top: 20px;
}

@media only screen and (max-width: 600px) {
    .header-logo {
        width: auto;
    }
    .header-logo img {
        width: 60vw;
    }
    .rotate {
        transform: rotate(-90deg);
        position: absolute;
        top: 20px;
        right: 20px;
    }
    .mobile-menu {
        background-color: transparent;
        display: flex;
        flex-direction: column;
        transition: height 0.3s ease;
    }
    .header-options ul {
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-start;
        padding-left: 0;
    }
}
.footer-main {
    width:100%;
    margin: 0 auto;
    background-color: #2a2b2b;
    padding: 20px 0;
    padding-bottom: 70px;
    height: auto;
}

.social-media-icons {
    width: 50%;
    height: 70px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 20px auto;
    margin-top:50px
}

a .social-media-icons-card {
    width:45px;
    height: 70px;
    margin:0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 20px;
    flex-direction: column;
    cursor: pointer;
    text-decoration: none;
}

.social-media-icons-image {
    width:45px;
    height: 45px;
}

.social-media-icons-p {
    text-align: center;
    font-size: 12px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    vertical-align: middle;
    padding: 15px 0;
    font-weight: 500;
    margin: 0;
}


.footer-text {
    margin: 20px auto;
    display: flex;
    width: 70%;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}
.footer-text h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    color: #fff;
    line-height:25px;
    font-weight: 600px;
    margin: 0;
}
.footer-text p {
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    color: #fff;
    line-height:20px;
    font-weight: 500px;
    margin: 0;
    margin-top: 10px;
}

.three-sections {
    width: 70%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0 auto;
    margin-top: 45px;
}

.section {
    height: 200px;
    background:transparent;
    width: 30%;
}
.separator {
    height: 180px;
    width:1px;
    background-color:#474444;
    border-radius: 1px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.section h3 {
    font-family: 'Montserrat', sans-serif;
    color:var(--googleBlue);
    font-size: 18px;
    margin: 10px auto;
    text-align: center;
    font-weight: 700;
    margin-top: 20px;
}
.section p {
    font-family: 'Montserrat', sans-serif;
    color: #eee;
    font-size: 13px;
    margin: 0px 5px 10px 5px;
    text-align: center;
    font-weight: 400;
    line-height:22px;
    margin-top: 15px;
}

.section span {
    width: 100%;
    padding: 0px 15px;
    height: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.section img {
    width: 20px;
    height: 20px;
    margin: 5px 5px 5px 5px;
}

.app-buttons-div {
    width: 80%;
    margin: 25px auto;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.app-button {
    width: 100px;
    height: 45px;
    border-radius: 40px;
    border: 2px solid ;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    font-size: 13px;
    font-weight: 600;
}


.copyright {
    font-family: 'Montserrat', sans-serif;
    color: #eee;
    font-size: 11px;
    margin: 20px auto;
    text-align: center;
    font-weight: 400;
    margin-bottom: 30px;  
}

@media only screen and (max-width: 600px) { 
    .three-sections {
        width: 100%;
        flex-direction: column;
    }
    .section {
        width: 90%;
        height: auto;
        margin: 10px auto; 
        padding: 0 10px;
        box-sizing: border-box; 
    }
    .separator {
        display: none;
    }
    .section span{
        width: auto;
        justify-content: center;
    }
}

.home-main-content {
   box-sizing: border-box;
   height: 100vh;
   align-items: center;
   scroll-behavior: smooth;
}

.home-main-content-inner {
    padding-top: 120px;
    background-color: transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

.home-main-content-text {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    padding-left: 10%;
    padding-bottom: 5%;
}

.home-main-content-text img {
    width: 90%;
}

.home-main-content-text h4 {
    font-family: var(--bigHeadingFontFamily);
    font-size: var(--heading6);
    font-weight: var(--bold);
    margin-top: 10%;
    text-align: center;
}

.home-main-content-text h6 {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--heading5);
    font-weight: var(--light);
    margin-top: 10%;
    text-align: center;
}

.home-main-content-image {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-main-content-image img {
    width: 90%;
    margin-bottom: 10%;
}

.home-main-content-down-arrow {
    position: absolute;
    width: 10%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    bottom: 10px;
    align-self: center;
    margin-left: 45%;
    cursor: pointer;
}

.home-main-content-down-arrow-arrow {
    padding: 8px;
    background-color: transparent;
    border-radius: 35px;
    /* box-shadow: 2px 2px 15px #0000002a; */
}

.hero {
    color: #111236;
    font-family: 'Montserrat', sans-serif;
    font-size: 1rem;
    line-height: 1.3rem;
    box-sizing: inherit;
    position: relative;
    display: flex;
    min-width: 10rem;
    height: 1.5rem;
    overflow: hidden;
    width: 100%;
    align-items: flex-end;
    align-self: flex-end;

}
.hero::after{
    box-sizing: inherit;
}
.hero::before{
    box-sizing: inherit;
}

.svg-curve {
    color: #111236;
    font-size: 1rem;
    line-height: 1.3rem;
    box-sizing: inherit;
    position: absolute;
    bottom: -1px;
    width: 700rem;
    height: 24px;
    margin-left: 50%;
    transform: translateX(-50%);
    fill: #2a2b2b;
    overflow: hidden;
}

.about-us-container {
    /* height:1500px;  */
    background-color: #2a2b2b; 
    top: -40px; 
    /* position: relative; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-us-container h3 {
    font-family: var(--bigHeadingFontFamily);
    font-size: var(--heading1);
    font-weight: var(--bold);
    color: #ffffff;
}

.about-us-container h4 {
    font-family: var(--bigHeadingFontFamily);
    font-size: var(--heading4);
    font-weight: var(--bold);
    color: #ffffff;
    margin-top: 100px;
}

.about-us-container p {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--heading5);
    font-weight: var(--thin);
    color: #ffffff;
    width: 60%;
    text-align: center;
    line-height: 30px;
    letter-spacing: 0.5px;
}

.features {
    margin: 40px auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 70%;
    justify-content: space-between;
}

.feature-item {
    width: 25%;
    background-color: transparent;
    margin: 0 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.feature-item-image {
    width: 10vw;
    height: 10vw;
    border-radius: 50%;
    background-color: #434343;
    display: flex;
    align-items: center;
    justify-content: center;
}

.feature-item-image img {
    width: 7vw;
    height: 7vw;
}

.feature-item h4 {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--heading4);
    font-weight: var(--regular);
    letter-spacing: 0.9px;
    margin: 20px 0 0 0;
    padding: 0;
    text-align: center;
}

.feature-item h6 {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--smallText);
    font-weight: var(--thin);
    letter-spacing: 0.9px;
    margin: 25px 0 10px 0;
    padding: 0;
    text-align: center;
    color: #f6f6f6;
}

@media only screen and (max-width: 600px) {
    .home-main-content {
        height: 95vh;
    }
    .home-main-content-inner {
        flex-direction: column;
        padding-top: 20px;
    }
    .home-main-content-text {
        width: 100%;
        padding: 0 5%;
    }
    .home-main-content-text img {
        width: 60%;
    }
    .home-main-content-text h6 {
        font-size: var(--verySmallText);
    }
    .home-main-content-image {
        width: 100%;
        overflow: hidden;
    }
    .home-main-content-image img {
        width: 120%;
    }
    .svg-curve {
        height: 30px;
        bottom: -1px;
    }
    .hero {
        height: 1.7rem;
        bottom: -1px;
    }
    .features {
        flex-direction: column;
        align-items: center;
    }
    .feature-item {
        width: 100%;
        margin: 20px auto;
    }
    .feature-item-image {
        width: 30vw;
        height: 30vw;
        border-radius: 30vw;
    }
    .feature-item-image img {
        width: 60%;
        height: 60%;
    }
}

.team-main-container {
    padding-top: 80px;
    display: flex;
    flex-direction: row;
}

.left-side {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    padding: 20px;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.left-side h2 {
    font-family: var(--bigHeadingFontFamily);
    font-size: var(--heading1);
    font-weight: var(--bold);
    color: var(--tanText);
}

.left-side p {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--heading6);
    font-weight: var(--regular);
    color: var(--tanText);
    text-align: center;
}

.right-side {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.team-members-core {
    box-sizing: border-box;
    padding: 10px 10%;
    background-color: transparent;
}

.team-members-core-inner {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.core-member-box {
    /* display: inline-block; */
    margin: 10px 20px;
    background-color:transparent;
    width: 20%;
    padding: 5px 5px 20px 5px;
}

.core-member-photo {
    width: 140px;
    height: 140px;
    border-radius: 100px;
    margin: 10% auto;
    background-color: #f6f6f6;
    overflow: hidden;
}

.core-member-photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    margin-top: 0;
}

.core-member-box h3 {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--heading5);
    font-weight: var(--medium);
    color: var(--tanText);
    text-align: center;
    margin-bottom: 5px;
    margin-top: 5px;
}

.core-member-box p {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--verySmallText);
    font-weight: var(--regular);
    color: var(--darkGrey);
    text-align: center;
    margin: 0;
}

.team-title {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--heading4);
    font-weight: var(--medium);
    color: var(--tanText);
    text-align: center;
    margin-top: 80px;
}

.complete-member-box {
    display: flex;
    height: 60px;
    flex-direction: column;
    align-items: flex-start;
    margin: 10px 20px;
    background-color:transparent;
    width: 20%;
    padding: 5px 5px 20px 5px;
}

.complete-member-photo-box {
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: blue;
}

.complete-member-box h3 {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--smallText);
    font-weight: var(--medium);
    color: var(--tanText);
    margin-bottom: 5px;
    margin-top: 5px;
}

.complete-member-box p {
    font-family: var(--smallHeadingFontFamily);
    font-size: var(--verySmallText);
    font-weight: var(--regular);
    color: var(--darkGrey);
    margin: 0;
}

@media only screen and (max-width: 600px) {
    .team-main-container {
        padding-top: 0;
    }
    .team-title {
        margin-top: 0;
    }
    .team-members-core-inner {
        flex-direction: column;
        align-items: center;
    }
    .core-member-box {
        width: 100%;
        padding: 0;
        margin: 0;
    }
    .complete-member-box {
        box-sizing: border-box;
        flex-direction: row;
        width: 100%;
        margin: 0;
        height: 50px;
        justify-content: space-between;
        align-items: center;
    }
}
.blog-list {
	padding-top: 120px;
	text-align: center;
	min-height: 100vh;
	font-family: var(--smallHeadingFontFamily);
	font-size: var(--heading6);
	font-weight: var(--regular);
	color: var(--tanText);
}

.blog-item {
	padding-bottom: 30px;
}

.blog-list h2 {
	font-family: var(--bigHeadingFontFamily);
	font-size: var(--heading1);
	font-weight: var(--bold);
	color: var(--tanText);
}

.blog-image {
	max-height: 400px;
	max-width: 600px;
}

.blog-title {
	font-family: var(--smallHeadingFontFamily);
	color: var(--darkText);
	font-size: var(--heading6);
	font-weight: bold;
}

@media only screen and (max-width: 600px) {
	.blog-list {
		padding-top: 20px;
	}
	.blog-item {
		width: 100%;
		margin: 0 auto;
		align-items: center;
	}
	.blog-image {
		width: 90%;
		border-radius: 10px;
	}
	.blog-title {
		font-size: var(--heading6);
		font-family: var(--smallHeadingFontFamily);
		width: 100%;
		align-self: center;
	}
	.blog-item p {
		margin: 0 auto;
		width: 70%;
		margin-bottom: 10px;
	}
}

